/* 
Primary green: #004D40
Light green: #69F0AE
Dark green: #00251A
*/

/* Font */

@font-face {
  font-family: 'roboto_slab';
  src: url('fonts/robotoslab-variablefont_wght-webfont.woff2') format('woff2'),
       url('fonts/robotoslab-variablefont_wght-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'roboto_slab_light';
  src: url('fonts/robotoslab-light-webfont.woff2') format('woff2'),
       url('fonts/robotoslab-light-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'roboto_mono';
  src: url('fonts/robotomono-variablefont_wght-webfont.woff2') format('woff2'),
       url('fonts/robotomono-variablefont_wght-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

/* Global */

.App {
  background-color: #004D40;
}

.content {
  background-color: white;
}

@media (prefers-color-scheme: dark) {
  .content {
    background-color: #00251A;
  }
}

.section-header {
  font-family: roboto_slab;
  font-size: 22px;
  padding: 54px 0px 8px 0px;
  margin: 0 32px 54px 32px;
  border-bottom: 1px solid #004D40;
  font-weight: normal;
}

.section-content {

}

.section-title {
  font-family: roboto_slab;
  font-size: 20px;
  font-weight: normal;
}

.section-text {
  font-family: roboto_slab_light;
  font-weight: normal;
  font-size: 16px;
}

@media (prefers-color-scheme: dark) {
  .section-header {
    color: white;
    border-bottom: 1px solid #69F0AE;
  }

  .section-title {
    color: white;
  }

  .section-text {
    color: white;
  }

  .section-link {
    color: white;
  }
}

.section-link {
  font-family: roboto_mono;
  font-size: 14px;
}

/* Nav */

.top-bar-container {
  display: flex;
  background-color: #004D40;
}

.logo {
  flex:	0 1 auto;
  margin: 24px 24px 24px 32px;
  width: 32px;
}

.link-container {
  flex: 1;
  display: flex;
  gap: 24px;
  flex-direction: row;
  align-items: flex-start;
  right: 0;
  font-size: 16px;
  margin: 24px 0 24px 0;
  overflow: auto;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.link-container::-webkit-scrollbar {
  display: none;
}

.link-container::before {
  content: "";
  position: absolute;
  top: 0;
  z-index: 150;
  width: 32px;
  margin-left: 15px;
  height: 90px;
  left: 72px;
  background: linear-gradient(270deg,rgba(9,10,11,0) 0,#004D40);
}

.link-container::after {
  content: "";
  position: absolute;
  top: 0;
  z-index: 150;
  width: 32px;
  height: 90px;
  right: 0;
  background: linear-gradient(90deg,rgba(9,10,11,0) 0,#004D40);
}

.link {
  color: #FFF;
  font-family: roboto_slab;
  flex: 0 1 auto;
  cursor: pointer;
  margin: 16px 0;
  z-index: 100;
}

.link-first {
  margin-left: 32px;
}

.link-last {
  margin-right: 32px;
}

@media only screen and (min-width: 588px) {
  .link-container {
    justify-content: flex-end;
  }
}

/* Header */

.header {
  background-color: #004D40;
  height: 100vh;
  width: 100%;
  text-align: center;
  background-image: url( 'assets/background.svg' );
  background-position: center top;
  background-size: 600px;
  background-position-y: 24px;
}

.header-content {
  margin: 0 auto;
  height: 100%;
}

.header-centered-content-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header-centered-content {
  z-index: 200;
}

.header-title {
  font-family: roboto_slab;
  color: #FFF;
  font-size: 7vw;
  margin: 40px 0px 0px 0px;
}

.header-image {
  width: 250px;
  box-shadow: 9px 9px 0px -4px #004d40, 9px 9px 0px 0px #fff;
}

.header-subtitle {
  font-family: roboto_mono;
  color: #FFF;
  font-size: 3vw;
  margin: 0;
}

/* About */

.about {
  max-width: 900px;
  margin: 0 auto;
}

.about-text {
  margin: 0px 32px 0 32px;
  padding-bottom: 16px;
}

/* Skills */

.skills {
  padding-bottom: 16px;
  max-width: 900px;
  margin: 0 auto;
}

.skills-item-left {
  background-color: #69F0AE;
  padding: 32px;
  margin: 32px 32px 0 0;
}

.skills-item-right {
  background-color: #69F0AE;
  padding: 32px;
  margin: 32px 0 0 32px;
  text-align: end;
}

.skills-title-container-left {
  display: flex;
}

.skills-title-container-right {
  display: flex;
  flex-direction: row-reverse;
}

.skills-image-left {
  margin: 4px 8px 16px 0;
  width: 24px;
  display: block;
}

.skills-image-left-white {
  margin: 4px 8px 16px 0;
  width: 24px;
  display: none;
}

.skills-image-right {
  margin: 4px 0 16px 8px;
  width: 24px;
  display: block;
}

.skills-image-right-white {
  margin: 4px 0 16px 8px;
  width: 24px;
  display: none;
}

.skills-text-spacing {
  margin-bottom: 16px;
}

@media (prefers-color-scheme: dark) {
  .skills-item-left {
    background-color: #004d40;
  }
  
  .skills-item-right {
    background-color: #004D40;
  }

  .skills-image-left {
    display: none;
  }

  .skills-image-right {
    display: none;
  }

  .skills-image-left-white {
    display: block;
  }

  .skills-image-right-white {
    display: block;
  }
}

/* Work/Projects */

.apps {
  max-width: 900px;
  margin: 0 auto;
}

.websites {
  max-width: 900px;
  margin: 0 auto;
}

.projects {
  max-width: 900px;
  margin: 0 auto;
}

.work-intro {
  margin: 0px 32px 0 32px;
  padding: 0 0 40px 0;
}

.work-image-container-left {
  position: relative;
}

.work-image-container-right {
  position: relative;
}

.work-title-left {
  background-color: rgba(105, 240, 174, 0.98);
  padding: 16px 32px;
  position: absolute;
  top: 64px;
  left: 0;
  z-index: 100;
}

.work-title-right {
  background-color: rgba(105, 240, 174, 0.98);
  padding: 16px 32px;
  position: absolute;
  top: 64px;
  right: 0;
  z-index: 100;
  text-align: end;
}

.work-image-left {
  width: calc(100% - 32px);
  margin: 16px 16px 0 16px;
}

.work-image-right {
  width: calc(100% - 32px);
  margin: 16px 16px 0 16px;
}

.work-text-left {
  background-color: rgba(105, 240, 174, 0.98);
  text-align: end;
  padding: 32px 32px 32px 16px;
  position: absolute;
  bottom: 72px;
  left: 32px;
}

.work-text-right {
  background-color: rgba(105, 240, 174, 0.98);
  text-align: start;
  padding: 32px 16px 32px 32px;
  position: absolute;
  bottom: 72px;
  right: 32px;
}

.work-link-left {
  padding: 16px;
  text-align: center;
  text-decoration: underline;
  cursor: pointer;
}

.work-link-right {
  padding: 16px;
  text-align: center;
  text-decoration: underline;
  cursor: pointer;
}

@media (prefers-color-scheme: dark) {
  .work-title-left {
    background-color: rgba(0, 77, 64, 0.95);
  }

  .work-title-right {
    background-color: rgba(0, 77, 64, 0.95);
  }

  .work-text-left {
    background-color: rgba(0, 77, 64, 0.95);
  }

  .work-text-right {
    background-color: rgba(0, 77, 64, 0.95);
  }

  .work-link-left {
    text-decoration-color: white;
  }

  .work-link-right {
    text-decoration-color: white;
  }
}

/* Articles */

.articles {
  padding-bottom: 54px;
  max-width: 900px;
  margin: 0 auto;
}

.article-item-left {
  background-color: #69F0AE;
  padding: 32px;
  margin: 32px 32px 0 0;
  cursor: pointer;
}

.article-item-right {
  background-color: #69F0AE;
  padding: 32px;
  margin: 32px 0 0 32px;
  text-align: end;
  cursor: pointer;
}

.article-text-spacing {
  margin-bottom: 16px;
}

.article-link {
  text-decoration: underline;
}

@media (prefers-color-scheme: dark) {
  .article-item-left {
    background-color: #004d40;
  }
  
  .article-item-right {
    background-color: #004D40;
  }

  .article-link {
    text-decoration-color: white;
  }
}

/* Contacts */

.contact-container {
  display: flex;
}

.contact-link {
  text-align: center;
  width: 100%;
  padding: 48px 0px 16px 0px;
}

.contact-icon {
  height: 32px;
  cursor: pointer;
  margin: 0px 8px;
  padding: 8px;
}

/* Footer */

.footer {
  text-align: center;
  background-color: #004D40;
  background-image: url( 'assets/background.svg' );
  background-position: center top;
  background-size: 600px;
}

.footer-text {
  font-family: roboto_mono;
  color: #FFF;
  margin: 0;
  padding-bottom: 48px;
  font-size: 14px;
}
 

@media only screen and (min-width: 600px) {
  .work-image-container-left {
    display: flex;
  }

  .work-image-container-right {
    display: flex;
    flex-direction: row-reverse;
  }

  .work-image-left{
    height: 350px;
    width: auto;
    margin: 16px 0 16px 32px;
  }

  .work-image-right{
    height: 350px;
    width: auto;
    margin: 16px 32px 16px 0;
  }

  .work-title-left{
    position: unset;
    margin: 16px 0 32px 32px;
  }

  .work-text-left{
    position: unset;
    margin-right: 32px;
  }

  .work-title-right{
    position: unset;
    margin: 16px 32px 32px 0;
  }

  .work-text-right{
    position: unset;
    margin-left: 32px;
  }

  .work-link-left {
    text-align: left;
    padding: 32px;
  }

  .work-link-right {
    text-align: right;
    padding: 32px;
  }
}
